.Card3 {
    margin-top: 2rem;
    display: flex;
    justify-content: space-evenly;
    margin-left: 30px;
    margin-bottom: 100px;
    gap: 20px;
}

.card {
    width: 32%;
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(48, 48, 49, 0.567);
    border-radius: 7px;
    cursor: pointer;
    padding: 1rem 0.8rem;
    margin-right: 30px;
}

.img {
    height: 200px;
    overflow: hidden;
    border-radius: 7px;
}

.img img {
    width: 100%;
    height: 100%;
    transition: 0.3s ease-in-out;
    border-radius: 7px;
}

.img:hover img {
    transform: scale(1.3);
}

.card h4 {
    font-size: 1.3rem;
    font-weight: 600;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    padding: 0.9rem 0 0.2rem 0;
}

.card p {
    font-weight: 400;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

@media screen and (max-width: 850px) {
    .t-card {
        width: 100%;
        margin-bottom: 1.5rem;
    }
    .Card3 {
        flex-wrap: wrap;
    }
    .card {
        flex-basis: 100%;
        margin-bottom: 20px;
    }
}